import React, { useEffect, useState } from 'react';
import gsap from 'gsap';
import { TextPlugin } from "gsap/TextPlugin";
import profile from "../Assets/profile.png";
import { missingLetters, avaliableLetters, tempMissingLetters } from "./Shared";
import "../Styles/About.css";

gsap.registerPlugin(TextPlugin);

function About() {

    const [wordCount, setWordCount] = useState(0);

    useEffect(() => {

        gsap.fromTo(".profile-image-container", .5, { scale: 0 }, { opacity: 1, scale: 1, delay: 1 })
        gsap.fromTo(".profile-image", .5, { y: 50 }, { y: 0, opacity: 1, ease: "back.out(4)", delay: 1.5 });

        let tl = gsap.timeline({ delay: 2, ease: "back.out(1.2)" });

        // set text
        tl.to('.heading', .1, { text: "Hi, I'm", y: "-100px" })
            .to('.text', .1, { text: "Mohamed Elhaj", y: "-100px" })

            //////////////////////////////////////////////////////////// First Cycle
            // drop down
            .to('.heading', .5, { y: 0, opacity: 1 })
            .to('.text', .5, { y: 0, opacity: 1 }, "-=.2")

            // hold
            .to('.text', 3, { x: 0 })

            // go down
            .to('.heading', .5, { y: "50px", opacity: 0 })
            .to('.text', .5, { y: "65px", opacity: 0 }, "-=.2")

            // change Text
            .to('.heading', .1, { text: "I'm a" })

            // go up
            .to('.heading', .1, { y: "-100" })
            .to('.text', .1, { y: "-100" })
            .to('.intro-text', .1, { height: 0 })

            // drop down
            .to('.heading', .5, { y: 0, opacity: 1 })


        // game
        tl.to('.letters-box', 1, { y: 0 }, "-=.5")
            .to('.buttons-box', 1, { y: 0 }, "-=.5")


        // game animation

    }, []);

    const revealLetter = (index, letter) => {
        document.querySelector(`.letter-${index}`).innerHTML = letter;
        if (wordCount === tempMissingLetters.length - 1) {
            gsap.to('.buttons-box', 1, { y: "200px" }, "-=.5");
            gsap.to('.success-message', 1, { y: "-100px", ease: "back.out(1.2)", delay: 1 });
            gsap.to('.success-message-svg', .5, { scale: 1, opacity: 1, delay: 1 })
        } else {
            setWordCount(wordCount + 1);
        }
    }

    const check = (e, letter) => {

        e.currentTarget.disabled = true;

        if (letter.index === -1) {
            e.currentTarget.classList.add('wrong');
        } else {
            e.currentTarget.classList.add('right');
            revealLetter(letter.index, letter.char);
        }
    }

    return <section id="about" className="about flex">
        <div className="container flex flex-column center p15" style={{ paddingTop: "100px" }}>

            {/* <div className="flex center profile-image-container">
                <img src={profile} className="profile-image" alt="profile" />
            </div> */}
            <div className="flex flex-column center">
                <div className="overflow-hidden intro-heading">
                    <h1 className="heading">Hi, I'm </h1>
                </div>
                <div className="overflow-hidden intro-text">
                    <h1 className="text">Mohamed Elhaj</h1>
                </div>
            </div>


            <div className="overflow-hidden">
                <div className="letters-box flex center" style={{ flexWrap: "wrap", marginTop: "20px" }}>
                    {avaliableLetters && avaliableLetters.map((letter, index) => (
                        <div key={index}>
                            {letter.char === ' ' ? <div className="letter no-border-letter">&nbsp;</div> :
                                <h1 className={`letter-${index} letter flex center`}>{letter.char}</h1>
                            }
                        </div>
                    ))}
                </div>
            </div>


            <div className="overflow-hidden">
                <div className="buttons-box overflow-hidden flex center" style={{ flexWrap: "wrap", marginTop: "20px", maxWidth: "400px" }}>
                    {missingLetters && missingLetters.map((letter, index) => (
                        <div key={index}>
                            <button onClick={(e) => check(e, letter)} className="letter-buttons">
                                {letter.char}
                            </button>
                        </div>
                    ))}
                </div>
            </div>

            <div className="overflow-hidden success-message flex center">
                <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="var(--green)" className="success-message-svg bi bi-check2" viewBox="0 0 16 16">
                    <path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z" />
                </svg>
            </div>


        </div>
    </section>;
}
export default About;